body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
    text-align: center;
}

#header-container {
    background-color: #3fc0a5;
    width: 100%;
    height: 160px;
}

#menu-item {
    margin-top:20px;
    display: flex;
    flex-direction: row;
    justify-content: center;
}
#menu-container{
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 20px;
    margin: 5px;

}

#menu-title {
    margin-top: 5px;
    color: #7f7f7f;
}

#menu-img {
    height: 50px;
    width: 50px;
    font-size: 1.5rem;
}
#root-view {
    display: flex;
    flex-direction: column;
}

#user-info {
    display: flex;
    flex-direction: row;
    align-items: center;
    width: 100%;
    height: 120px;
}

#goods-info {
    flex-direction: column;
    display: flex;
}

#user-head-img {
    width: 50px;
    border-radius: 25px;
    margin: 20px;
    height: 50px;
}

#user-name {
    font-size: 1.0rem;
    color: black;
}

#goods-time {
    font-size: 0.5rem;
    color: #7f7f7f;
}

#goods-img {
    width: 100%;
    margin-right: 5px;
    margin-top: 5px;
    margin-left: 5px;
}

#goods-title {
    margin: 15px;
    font-size: 1.1rem;
    color: black;
}

#goods-price {
    color: red;
    margin: 10px;
}

#bottom {
    height: 65px;
    width: 100%;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    margin-left: 5px;
    margin-right: 5px;
    background-color: #99999999;
    position: fixed;
    bottom: 0;
}

#app_logo {
    height: 40px;
    width: 40px;
    align-self: center;
    margin-left: 10px;
}

#download-now {
    background-color: #009688;
    color: #fff;
    align-self: center;
    text-align: center;
    font-size: 0.6rem;
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
    padding-left: 10px;
    padding-top: 5px;
    padding-bottom: 5px;
    padding-right: 10px;
    margin-right: 20px;
}

#herf-app {
    text-decoration: none;
    color: #fff;
}

#bottom-app-info {
    display: flex;
    flex-direction: row;
}

#app-info {
    justify-content: center;
    margin-left: 15px;
    display: flex;
    flex-direction: column;
}

#app-name {
    color: #f6f6f6;
    font-size: 1.0rem;
}

#app-desc {
    margin-top: 2px;
    color: #f6f6f6;
    font-size: 0.5rem;
}
